'use client';

import { useState, useCallback } from 'react';
import { Upload, FileText, AlertCircle, CheckCircle, X } from 'lucide-react';
import { uploadFile } from '../lib/api-client';
import { UploadResult } from '../types/enterprise';

export default function UploadPage() {
  const [isDragging, setIsDragging] = useState(false);
  const [uploadResult, setUploadResult] = useState<UploadResult | null>(null);
  const [isUploading, setIsUploading] = useState(false);

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  }, []);

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
  }, []);

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    const files = Array.from(e.dataTransfer.files);
    if (files.length > 0) {
      handleFileUpload(files[0]);
    }
  }, []);

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      handleFileUpload(files[0]);
    }
  };

  const handleFileUpload = async (file: File) => {
    setIsUploading(true);
    setUploadResult(null);

    try {
      // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º —Ñ–∞–π–ª –Ω–∞ –±—ç–∫–µ–Ω–¥
      const result = await uploadFile(file);
      setUploadResult(result);
    } catch (error) {
      setUploadResult({
        success: false,
        message: `–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ–±—Ä–∞–±–æ—Ç–∫–µ —Ñ–∞–π–ª–∞: ${(error as Error).message}`,
        processedCount: 0,
        errorCount: 1,
        errors: [(error as Error).message]
      });
    } finally {
      setIsUploading(false);
    }
  };

  const clearResults = () => {
    setUploadResult(null);
  };

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      {/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">–ó–∞–≥—Ä—É–∑–∫–∞ –¥–∞–Ω–Ω—ã—Ö –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏–π</h1>
        <p className="text-gray-600 mt-2">
          –ó–∞–≥—Ä—É–∑–∏—Ç–µ —Ñ–∞–π–ª CSV –∏–ª–∏ Excel —Å –¥–∞–Ω–Ω—ã–º–∏ –æ –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏—è—Ö –ú–æ—Å–∫–≤—ã –¥–ª—è –∞–Ω–∞–ª–∏–∑–∞
        </p>
      </div>

      {/* –ó–æ–Ω–∞ –∑–∞–≥—Ä—É–∑–∫–∏ */}
      <div className="bg-white rounded-lg shadow-sm border">
        <div
          className={`
            border-2 border-dashed rounded-lg p-12 text-center transition-colors
            ${isDragging 
              ? 'border-blue-400 bg-blue-50' 
              : 'border-gray-300 hover:border-gray-400'
            }
          `}
          onDragOver={handleDragOver}
          onDragLeave={handleDragLeave}
          onDrop={handleDrop}
        >
          <div className="flex flex-col items-center space-y-4">
            <div className="p-4 bg-gray-100 rounded-full">
              <Upload className="w-8 h-8 text-gray-600" />
            </div>

            <div>
              <h3 className="text-lg font-semibold text-gray-900">
                –ü–µ—Ä–µ—Ç–∞—â–∏—Ç–µ —Ñ–∞–π–ª —Å—é–¥–∞ –∏–ª–∏ –≤—ã–±–µ—Ä–∏—Ç–µ —Ñ–∞–π–ª
              </h3>
              <p className="text-gray-500 mt-1">
                –ü–æ–¥–¥–µ—Ä–∂–∏–≤–∞—é—Ç—Å—è —Ñ–æ—Ä–º–∞—Ç—ã: CSV, Excel (XLSX, XLS)
              </p>
            </div>

            <div className="flex flex-col sm:flex-row gap-4">
              <label className="bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 cursor-pointer transition-colors">
                <input
                  type="file"
                  accept=".csv,.xlsx,.xls"
                  onChange={handleFileSelect}
                  className="hidden"
                />
                –í—ã–±—Ä–∞—Ç—å —Ñ–∞–π–ª
              </label>

              <button
                onClick={() => {
                  // –ó–∞–≥—Ä—É–∂–∞–µ–º –¥–µ–º–æ–Ω—Å—Ç—Ä–∞—Ü–∏–æ–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ
                  const demoResult: UploadResult = {
                    success: true,
                    message: '–ó–∞–≥—Ä—É–∂–µ–Ω—ã –¥–µ–º–æ–Ω—Å—Ç—Ä–∞—Ü–∏–æ–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ',
                    processedCount: 100,
                    errorCount: 5,
                    errors: [
                      '–°—Ç—Ä–æ–∫–∞ 15: –ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω–∞—è –æ—Ç—Ä–∞—Å–ª—å',
                      '–°—Ç—Ä–æ–∫–∞ 23: –û—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç –∞–¥—Ä–µ—Å',
                      '–°—Ç—Ä–æ–∫–∞ 34: –ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–æ–≤',
                      '–°—Ç—Ä–æ–∫–∞ 67: –ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π —Ä–µ–≥–∏–æ–Ω',
                      '–°—Ç—Ä–æ–∫–∞ 89: –ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω–∞—è –≤—ã—Ä—É—á–∫–∞'
                    ]
                  };
                  setUploadResult(demoResult);
                }}
                className="border border-gray-300 text-gray-700 px-6 py-3 rounded-lg hover:bg-gray-50 transition-colors"
              >
                –ó–∞–≥—Ä—É–∑–∏—Ç—å –¥–µ–º–æ-–¥–∞–Ω–Ω—ã–µ
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* –ò–Ω–¥–∏–∫–∞—Ç–æ—Ä –∑–∞–≥—Ä—É–∑–∫–∏ */}
      {isUploading && (
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-6">
          <div className="flex items-center space-x-3">
            <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
            <div>
              <p className="font-medium text-blue-900">–û–±—Ä–∞–±–æ—Ç–∫–∞ —Ñ–∞–π–ª–∞...</p>
              <p className="text-blue-700 text-sm">–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–¥–æ–∂–¥–∏—Ç–µ</p>
            </div>
          </div>
        </div>
      )}

      {/* –†–µ–∑—É–ª—å—Ç–∞—Ç –∑–∞–≥—Ä—É–∑–∫–∏ */}
      {uploadResult && (
        <div className={`
          rounded-lg p-6 border
          ${uploadResult.success 
            ? 'bg-green-50 border-green-200' 
            : 'bg-red-50 border-red-200'
          }
        `}>
          <div className="flex items-start justify-between">
            <div className="flex items-start space-x-3">
              {uploadResult.success ? (
                <CheckCircle className="w-6 h-6 text-green-600 flex-shrink-0" />
              ) : (
                <AlertCircle className="w-6 h-6 text-red-600 flex-shrink-0" />
              )}
              <div>
                <h3 className={`font-semibold ${
                  uploadResult.success ? 'text-green-900' : 'text-red-900'
                }`}>
                  {uploadResult.success ? '–ó–∞–≥—Ä—É–∑–∫–∞ –∑–∞–≤–µ—Ä—à–µ–Ω–∞' : '–û—à–∏–±–∫–∞ –∑–∞–≥—Ä—É–∑–∫–∏'}
                </h3>
                <p className={`mt-1 ${
                  uploadResult.success ? 'text-green-700' : 'text-red-700'
                }`}>
                  {uploadResult.message}
                </p>
              </div>
            </div>
            <button
              onClick={clearResults}
              className="text-gray-400 hover:text-gray-600"
            >
              <X className="w-5 h-5" />
            </button>
          </div>

          {/* –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ */}
          <div className="mt-4 grid grid-cols-1 sm:grid-cols-2 gap-4">
            <div className="bg-white p-4 rounded-lg border">
              <div className="text-center">
                <p className="text-2xl font-bold text-green-600">{uploadResult.processedCount}</p>
                <p className="text-sm text-gray-600">–û–±—Ä–∞–±–æ—Ç–∞–Ω–æ –∑–∞–ø–∏—Å–µ–π</p>
              </div>
            </div>
            <div className="bg-white p-4 rounded-lg border">
              <div className="text-center">
                <p className="text-2xl font-bold text-red-600">{uploadResult.errorCount}</p>
                <p className="text-sm text-gray-600">–û—à–∏–±–æ–∫</p>
              </div>
            </div>
          </div>

          {/* –û—à–∏–±–∫–∏ */}
          {uploadResult.errors.length > 0 && (
            <div className="mt-4">
              <h4 className="font-medium text-gray-900 mb-2">–ù–∞–π–¥–µ–Ω–Ω—ã–µ –æ—à–∏–±–∫–∏:</h4>
              <div className="bg-white rounded-lg border max-h-40 overflow-y-auto">
                <div className="p-4 space-y-2">
                  {uploadResult.errors.slice(0, 10).map((error, index) => (
                    <div key={index} className="flex items-start space-x-2">
                      <div className="w-2 h-2 bg-red-400 rounded-full mt-2 flex-shrink-0"></div>
                      <p className="text-sm text-gray-700">{error}</p>
                    </div>
                  ))}
                  {uploadResult.errors.length > 10 && (
                    <p className="text-sm text-gray-500 italic">
                      –ò –µ—â—ë {uploadResult.errors.length - 10} –æ—à–∏–±–æ–∫...
                    </p>
                  )}
                </div>
              </div>
            </div>
          )}
        </div>
      )}

      {/* –ò–Ω—Å—Ç—Ä—É–∫—Ü–∏–∏ */}
      <div className="bg-gray-50 rounded-lg p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">–§–æ—Ä–º–∞—Ç –¥–∞–Ω–Ω—ã—Ö</h3>
        <div className="space-y-4">
          <p className="text-gray-700">
            –§–∞–π–ª –¥–æ–ª–∂–µ–Ω —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Å–ª–µ–¥—É—é—â–∏–µ —Å—Ç–æ–ª–±—Ü—ã:
          </p>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">name</span> - –ù–∞–∑–≤–∞–Ω–∏–µ –ø—Ä–µ–¥–ø—Ä–∏—è—Ç–∏—è
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">industry</span> - –û—Ç—Ä–∞—Å–ª—å
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">region</span> - –†–µ–≥–∏–æ–Ω –ú–æ—Å–∫–≤—ã
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">employees</span> - –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–æ–≤
              </div>
            </div>
            <div className="space-y-2">
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">revenue</span> - –í—ã—Ä—É—á–∫–∞ (—Ä—É–±.)
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">address</span> - –ê–¥—Ä–µ—Å
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">phone</span> - –¢–µ–ª–µ—Ñ–æ–Ω (–æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω–æ)
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">email</span> - Email (–æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω–æ)
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
